@charset "UTF-8";
article,aside,details,figcaption,figure,footer,header,main,menu,nav,section{display:block}
audio,canvas,progress,video{display:inline-block}
article,aside,audio,blockquote,body,canvas,caption,dd,details,div,dl,dt,embed,fieldset,figcaption,figure,footer,form,h1,h2,h3,h4,h5,h6,header,html,iframe,legend,li,main,menu,nav,ol,p,pre,section,summary,table,tbody,td,template,textarea,tfoot,th,thead,tr,ul,video{margin:0;padding:0;border:0;font-size:inherit;font-family:inherit;box-sizing:border-box;word-break:break-all}
a,em,span{display:inline-block;margin:0;padding:0;box-sizing:border-box}
b,strong{font-weight:600}
address,cite,dfn,em,i{font-style:normal}
img{border:none;vertical-align:middle}
audio,img,video{max-width:100%;max-height:100%}
label{display:inline-block;margin:0;cursor:pointer}
iframe{display:block;max-width:100%}
summary{display:list-item;cursor:pointer}
ul,ul>li{list-style:none}
ol{overflow:hidden;position:relative;counter-reset:number}
ol>li{counter-increment:number}
ol>li:before{content:counter(number,decimal) "."}
table{width:100%;border-collapse:collapse;border-spacing:0}
caption{overflow:hidden;position:relative;left:0;top:0;width:1px;height:1px;background:0 0;font-size:0;color:transparent;line-height:0;text-indent:100%}
legend{overflow:hidden;position:absolute;width:1px;height:1px;background:0 0;font-size:0;color:transparent;line-height:0}
fieldset{border:none;min-width:0}
:after,:before{display:none;content:''}
.skip{overflow:hidden;position:absolute;width:1px;height:1px;background:0 0;font-size:0!important;color:transparent!important;line-height:0}
button,input,optgroup,select,textarea{margin:0;padding:0;border:0;box-sizing:border-box;color:inherit;font-size:inherit;font-family:inherit;line-height:inherit;vertical-align:middle}
button,input,select{display:inline-block}
button,input{overflow:visible}
input:focus{outline:none;}
input[type=search],input[type=text]{-webkit-appearance:none}
input[type=search]{outline-offset:-2px;-webkit-appearance:none}
input[type=search]::-webkit-search-decoration{-webkit-appearance:none}
input[type=checkbox],input[type=radio]{padding:0;border:none}
button,select{text-transform:none}
[type=button],[type=reset],[type=submit],button{overflow:visible;border-color:transparent;background-color:transparent;cursor:pointer}
textarea{display:block;overflow:auto;resize:vertical}
input[disabled]{background-color:#dde1e8}
select:disabled{background-color:#dde1e8}
a:link{color:#141218; text-decoration:none;}
a:visited{color:#141218; text-decoration:none;}
a:hover{color:#141218; text-decoration:none;}
a:active{color:#141218; text-decoration:none;}